import uCharts from '@/components/u-charts/u-charts.js';
import {
	api,
	postajax
} from '@/utils/api.js'
import {
	fileUrl
} from '@/utils/index.js'
var _self;
var canvaArcbar1;

export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			cWidth3: '', //圆弧进度图
			cHeight3: '', //圆弧进度图
			arcbarWidth: '', //圆弧进度图，进度条宽度,此设置可使各端宽度一致
			pixelRatio: 1,
			topic: {},
			detail: {},
			voiceRecord: [],
			innerAudioContext: null
		}
	},
	onLoad(option) {
		_self = this;











		this.cWidth3 = uni.upx2px(250); //这里要与样式的宽高对应
		this.cHeight3 = uni.upx2px(250); //这里要与样式的宽高对应
		this.arcbarWidth = uni.upx2px(34);
		this.cWidth = uni.upx2px(700); //这里要与样式的宽高对应
		this.cHeight = uni.upx2px(500); //这里要与样式的宽高对应

		/* 获取主题信息 */
		this.topic = JSON.parse(decodeURIComponent(option.info));
		console.log(this.topic)
		this.init();
		this.innerAudioContext = uni.createInnerAudioContext();
		this.innerAudioContext.onPlay(() => {
			console.log('开始播放');
		});
		this.innerAudioContext.onEnded(() => {
			this.innerAudioContext.src = '';
			console.log('开始结束');
		})
	},
	onPullDownRefresh() {
		this.init();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		init() {
			this.getstudentAnalyseDetail();
			this.getvoiceRecord();
		},
		getstudentAnalyseDetail() {
			
			postajax(api.studentAnalyseDetail, {
				"topicCode": this.topic.topicCode,
				"stuCode": uni.getStorageSync('userinfo').stuCode
			}).then(da => {
				{
					console.log(da)
					if (da.code == 0) {
						this.detail = da.data;
						this.getServerData();
					}
				}
			})
		},
		getServerData() {
			let Arcbar2 = {
				series: []
			};
			let Arcbar3 = {
				series: []
			};
			//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
			Arcbar2.series = [{
				name: '正确率',
				"data": this.detail.stuCorrectAccuracy?this.detail.stuCorrectAccuracy.slice(0, this.detail.stuCorrectAccuracy.length - 1) / 100:0,
				"color": "#81a3e2"
			}];
			Arcbar3.series = [{
				name: '击败人数比',
				"data":this.detail.defeatStuCount? this.detail.defeatStuCount.slice(0, this.detail.defeatStuCount.length - 1) / 100:0,
				"color": "#99c2cc"
			}];
			_self.showArcbar2("canvasArcbar2", Arcbar2);
			_self.showArcbar3("canvasArcbar3", Arcbar3);

		},

		showArcbar2(canvasId, chartData) {
			new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'arcbar',
				fontSize: 11,
				legend: false,
				title: {
					name: Math.round(chartData.series[0].data * 100) + '%',
					color: chartData.series[0].color,
					fontSize: 25 * _self.pixelRatio
				},
				subtitle: {
					name: chartData.series[0].name,
					color: '#666666',
					fontSize: 12 * _self.pixelRatio
				},
				extra: {
					arcbar: {
						type: 'circle',
						width: _self.arcbarWidth * _self.pixelRatio, //圆弧的宽度
						backgroundColor: '#e28193',
						startAngle: 1.25
					}
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth3 * _self.pixelRatio,
				height: _self.cHeight3 * _self.pixelRatio,
				dataLabel: true,
			});

		},
		showArcbar3(canvasId, chartData) {
			new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'arcbar',
				fontSize: 11,
				legend: false,
				title: {
					name: Math.round(chartData.series[0].data * 100) + '%',
					color: chartData.series[0].color,
					fontSize: 25 * _self.pixelRatio
				},
				subtitle: {
					name: chartData.series[0].name,
					color: '#333',
					fontSize: 12 * _self.pixelRatio
				},
				extra: {
					arcbar: {
						type: 'circle', //整圆类型进度条图
						width: _self.arcbarWidth * _self.pixelRatio, //圆弧的宽度
						backgroundColor: '#e281b6',
						startAngle: 1.25,
					}
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth3 * _self.pixelRatio,
				height: _self.cHeight3 * _self.pixelRatio,
				dataLabel: true,
			});

		},
